<template>

  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" v-bind:to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" v-bind:to="{name: 'cart'}">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" v-bind:to="{name: 'orderInfo'}">
            Оформление заказа
          </router-link>
        </li>
      </ul>

      <h1 class="content__title">
        Заказ оформлен <span>№ {{ information.id }}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет письмо с&nbsp;деталями заказа. 
            Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                Получатель
              </span>
              <span class="dictionary__value">
                {{ information.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Адрес доставки
              </span>
              <span class="dictionary__value">
                {{ information.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Телефон
              </span>
              <span class="dictionary__value">
                {{ information.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{ information.email }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Способ оплаты
              </span>
              <span class="dictionary__value">
                картой при получении
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="position in positions" :key="position.id">
              <h3> {{ position.product.title }}</h3>
              <b> {{ position.product.price * position.quantity | numberFormat }} ₽</b>
              <span>Артикул: {{ position.product.id }}</span>
            </li>
          </ul>
          
          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b> {{ productsCounter }}</b> на сумму <b> {{ information.totalPrice | numberFormat }}₽</b></p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>

	import numberFormat from '@/helpers/numberFormat';
	import productsCounter from '@/helpers/productsCounter';
	
	export default {
		data() {
			return {
			}
		},
		filters: {
			numberFormat,
		},
		computed: {
			information(){
				return this.$store.state.orderInfo;
			},
			positions(){
				return this.information.basket.items;
			},
			productsCounter,
		},
		created(){
			if (this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id) {
				return;
					/* Добавили проверку на наличие в хранилище переменной orderInfo в разделе .state
						 и соответствие в данных по id с динамическим id в адресе страницы, чтобы
						 лишний раз не вызывать обновлений и не нагружать сервер */
			}
			this.$store.dispatch('loadOrderInfo', this.$route.params.id);
				/*при перезагрузке страницы мы обновляем данные по заказу, вызывая
					функцию-действие, которая запускает функцию-мутацию из хранилища, обновляющую
					нужные нам данные, чтобы они не пропадали при перезагрузке страницы, если
					они перед этим были получены при переходе со страницы оформления заказа.
					
					Поскольку у нас в адресе страницы есть id-заказа, то есть число, мы его и передаём
					в функцию в качесте аргумента обращаясь к свойству .params библиотеки "vue-router"
					*/
			}
	}
</script>