<template>

	<aside class="filter">
	
		<h2 class="filter__title">Фильтры</h2>

		<form class="filter__form form" action="#" method="get" @submit.prevent="submit">
			<!-- @submit -- это отслеживания стандартного осбытия, а ="submit" -- это уже наш собственный метод (см. в скриптах) -->
		
			<fieldset class="form__block">
				<legend class="form__legend">Цена</legend>
				<label class="form__label form__label--price">
					<input class="form__input" type="text" name="min-price" v-model.number="currentPriceFrom">
					<span class="form__value">От</span>
				</label>
				<label class="form__label form__label--price">
					<input class="form__input" type="text" name="max-price" v-model.number="currentPriceTo">
					<span class="form__value">До</span>
				</label>
			</fieldset>

			<fieldset class="form__block">
				<legend class="form__legend">Категория</legend>
				<label class="form__label form__label--select">
					<select class="form__select" type="text" name="category" v-model.number="currentCategoryId">
						<option value="0">Все категории</option>
						<option :value="category.id" v-for="category in categories" :key="category.id"> {{ category.title }} </option>
					</select>
				</label>
			</fieldset>

			<fieldset class="form__block">
				<legend class="form__legend">Цвет</legend>
				<ul class="colors">
					<li class="colors__item" v-for="color in colors" :key="color.id">
						<label class="colors__label" >
							<input class="colors__radio sr-only" type="radio" name="color" :value="color.id" v-model="currentColorId">
							<span class="colors__value" :style="{ backgroundColor: color.code}">
							</span>
						</label>
					</li>
				</ul>
			</fieldset>

			<fieldset class="form__block">
				<legend class="form__legend">Объемб в ГБ</legend>
				<ul class="check-list">
					<li class="check-list__item">
						<label class="check-list__label">
							<input class="check-list__check sr-only" type="checkbox" name="volume" value="8" checked="">
							<span class="check-list__desc">
								8 
								<span>(313)</span>
							</span>
						</label>
					</li>
					<li class="check-list__item">
						<label class="check-list__label">
							<input class="check-list__check sr-only" type="checkbox" name="volume" value="16">
							<span class="check-list__desc">
								16 
								<span>(461)</span>
							</span>
						</label>
					</li>
					<li class="check-list__item">
						<label class="check-list__label">
							<input class="check-list__check sr-only" type="checkbox" name="volume" value="32">
							<span class="check-list__desc">
								32 
								<span>(313)</span>
							</span>
						</label>
					</li>
					<li class="check-list__item">
						<label class="check-list__label">
							<input class="check-list__check sr-only" type="checkbox" name="volume" value="64">
							<span class="check-list__desc">
								64 
								<span>(313)</span>
							</span>
						</label>
					</li>
					<li class="check-list__item">
						<label class="check-list__label">
							<input class="check-list__check sr-only" type="checkbox" name="volume" value="128">
							<span class="check-list__desc">
								128 
								<span>(313)</span>
							</span>
						</label>
					</li>
					<li class="check-list__item">
						<label class="check-list__label">
							<input class="check-list__check sr-only" type="checkbox" name="volume" value="264">
							<span class="check-list__desc">
								264 
								<span>(313)</span>
							</span>
						</label>
					</li>
				</ul>
			</fieldset>

			<button class="filter__submit button button--primery" type="submit">
				Применить
			</button>
			<button class="filter__reset button button--second" type="button" @click.prevent="reset">
				<!-- Атрибут события клик @click с добавочным методом .prevent (аналогичен .preventDefault()) со значением нашего собственного метода "reset" -->
				Сбросить
			</button>
			
		</form>
		
	</aside>
			
</template>

<script>

	import axios from 'axios';
		// импортировали библиотеку axios (для оформления запросов на сервер)
	import {API_BASE_URL} from '../config';
	  // импортируем базовый адрес сайта из одного места (для удобства!)
	
	export default {
	
		data(){
			return {
				currentPriceFrom: 0,
				currentPriceTo: 0,
				currentCategoryId: 0,
				currentColorId: '',
				categoriesData: null,
				colorsData: null,
			};
		},
		props: ['priceFrom', 'priceTo', 'categoryId', 'variantColorId'],
		computed: {
			categories(){
				return this.categoriesData ? this.categoriesData.items : [];
					/* так же здесь в список категорий мы возвращаем нужное свойство из полученного с сервера объекта,
						 в нём должен быть список категорий. */
			},
			colors(){
				return this.colorsData ? this.colorsData.items : [];
					/* возвращаем список массив с объектами, содержащими информацию о цветах товаров,
						 потом (в <tamplate> мы разберём по составляющим с помощью v-for) */
			}
		},
		methods: {
			submit(){
				this.$emit('update:priceFrom', this.currentPriceFrom);
				this.$emit('update:priceTo', this.currentPriceTo);
				this.$emit('update:categoryId', this.currentCategoryId);
				this.$emit('update:variantColorId', this.currentColorId);
					// подзабыл подробности работы $emit()... нужно освежить...
			},
			reset(){
				this.$emit('update:priceFrom', 0);
				this.$emit('update:priceTo', 0);
				this.$emit('update:categoryId', 0);
				this.$emit('update:variantColorId', '#000000');
			},
			loadCategories(){
				axios.get(API_BASE_URL + '/api/productCategories')
					.then(response => this.categoriesData = response.data)
					.catch(error => console.log(error));
						/*Здесь всё аналогично в компоненте MainPage.vue в переменную, отвечающую за хранение результата
							промиса запроса на сервер записываем ответ с сервера */
			},
			loadColors(){
				axios.get(API_BASE_URL + '/api/colors')
					.then(response => {
						this.colorsData = response.data;
						//console.log(this.colorsData.items);
					})
					.catch(error => console.log(error));
			},
		},
		watch: {
			// свойство "watch" для описания функции/метода Vue по наблюдению за другими свойствами и ...
			// данными, при изменении которых эта функция-наблюдатель производит какие-то действия или ...
			// вызывает другие методы/функции
				
				priceFrom(value){
					this.currentPriceFrom = value;
				},
				priceTo(value){
					this.currentPriceTo = value;
				},
				categoryId(value){
					this.currentCategoryId = value;
				},
				variantColorId(value){
					this.currentColorId = value;
				},
				
				
					// то есть мы используем идентичное название принимаемых извне значений (см. раздел "props") но уже
					// как функции, которые принимают значение (value) и передают его ещё куда-то уже во внутренние данные,
					// в данном случае в текущие значения полей формы, обеспечивая обратную связь между двумя параметрами
					// не связанными какими-то специальными методами.
		},
		created(){
			// при загрузке компонента мы вызываем метод loadCategories, "хук" created() -- это нативная функция Vue.
			this.loadCategories();
			this.loadColors();
		},
	
	}
	
</script>