import Vue from 'vue';
// Подключаем сюда установленную бибилиотеку
	import VueRouter from 'vue-router';
// Импортируем наш компоненты с разметкой и функционалом разных страниц сайта
	import MainPage from '@/pages/MainPage';
	import ProductPage from '@/pages/ProductPage';
	import CartPage from '@/pages/CartPage';
	import NotFoundPage from '@/pages/NotFoundPage';
	import OrderPage from '@/pages/OrderPage';
	import OrderInfoPage from '@/pages/OrderInfoPage';

Vue.use(VueRouter);

const routes = [
	// внутри константы routes создаём массив объектов, в которых осуществляем связку компонентов страниц с определённым адресом
	// для адресной строки (иначе у нас получается одностроничное приложение, что не очень удобно в некоторых ситуациях для пользователя)
	// чтобы пользователи могли давать друг другу ссылки на разные страницы сайта (маршруты в строках, расположенных выше других
	// имеют и более высокий приоритет), маршрут для ненайденной страницы с path: '*' нужно расположить ниже остальных:
	{name: 'main', component: MainPage, path: '/'},
	{name: 'cart', component: CartPage, path: '/cart'},
	{name: 'product', component: ProductPage, path: '/product/:id'},
			// ^-- обратить внимание, что адрес с динамическим сегментом, поэтому он указывается через двоеточие (:id) 
	{name: 'notFound', component: NotFoundPage, path: '*'},
	{name: 'order', component: OrderPage, path: '/order'},
	{name: 'orderInfo', component: OrderInfoPage, path: '/order/:id'}
			// ^-- подправили адресацию, которая должна зависеть от id заказа
	
];

const router = new VueRouter({
  routes,
});

export default router;
