import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';
// import strings from './m3-home-task-variables';
// import showAlert from './m3-home-task-function';

Vue.config.productionTip = false;

/* Теперь, чтобы мы могли принять данные из другого файла,
нужно указать те же самые переменные из того файла -- из которого мы их импортируем --
а так же указать адрес этого файла, от куда мы хотим взять эти переменные */

new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount('#app');

