<template>

  <main class="content container">
	
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        152 товара
      </span>
    </div>

    <div class="content__catalog">
			
			<ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo" :category-id.sync="filterCategoryId" :variant-color-id.sync="filterColorId"></ProductFilter>
				<!-- :price-from -- это то же самое, что и v-bind:price-from, то есть специальный атрибут Vue, с помощью, которого мы... -->
				<!-- определяем название передаваемого внутрь компонента Vue  значения, которое должно в данном случае быть равно... -->
				<!-- props: ['priceFrom'] и т.д. То есть в названии атрибута мы можем использовать "кебаб-кейс", но в наименованиях уже -->
				<!-- внутри комопнента используем синтаксис javascript, то есть "камелКейс", иначе ничего не будет работать! -->

			<section class="catalog">
			
				<div v-if="productsLoading"> Загрузка списка товров... </div>
					
					<!-- Отображение "спиннера" пока productsLoading = true (оно будет false, как только мы получим ответ -->
					<!-- сервера при запросе с помощью метода loadProducts()) -->
					
				<div v-if="productsLoadingFailed"> 
					Произошла ошибка при загрузке данных! 
					<button @click.prevent = "loadProducts">
						Попробовать ещё раз 
					</button>
				</div>
				
					<!-- Сообщение об ошибке выведется, если при запросе была ошибка, "спиннер" будет убран  -->
					<!-- в любом случае (см. метод loadProducts()), внутри унопка для перезапуска метода загрузки страницы -->
		
				<ProductList v-bind:products="products"></ProductList>
				
					<!-- Здесь мы прикрепляем импортируемый из .js-файла список продуктов, который... -->
					<!-- ...внутри директивы 'v-for' будет обработан и каждый элемент списка будет уже как 'product'. -->
					<!-- Далее смотри файлы 'ProductItem.vue' и 'ProductList.vue'. -->
					
				<BasePagination v-model="page" v-bind:count="countProducts" v-bind:per-page="productsPerPage"></BasePagination>
				
					<!-- с помощью директивы (атрибута тега шаблона Vue) мы... -->
					<!-- ... экспортируем внутрь этого шаблона значения из data() текущей страницы: номер страницы, общее количество... -->
					<!-- ... товаров, количество позиций на страницу, которые передаём тем самым внутрь шаблона, которые потом там -->
					<!-- ... обрабатываются (см. name of a props in vue template script ). -->
					<!-- ... причём в описании имени в теге шаблона 'camelCase' запрещён, он должен записываться в стиле -->
					<!-- ... кебаб-кейс, а данном случае "per-page" превратится в "perPage" свойства "props" внутри данного шаблона -->
		
			</section>
			
    </div>
		
  </main>

</template>

<script>
	import ProductFilter from '@/components/ProductFilter.vue';
	import ProductList from '@/components/ProductList.vue';
	import ProductItem from '@/components/ProductItem.vue';
	import BasePagination from '@/components/BasePagination.vue';
	import axios from 'axios';
		// импортировали бибилиотеку axios
	import {API_BASE_URL} from '../config';
	  // импортируем базовый адрес сайта из одного места (для удобства!
	
		// (!) символ "@" -- это синоним абсолютного пути до папки "src" во Vue -- очень удобно прикрепляем
		// работе и переносе расопложения файлов компонентов указывать абсолютный путь до файлов
	
	export default {
		components: { ProductFilter, ProductList, ProductItem, BasePagination },
			// здесь не забываем прописывать все компоненты, которые мы используем в шаблоне (раздел <template>)
		data() {
			return {
					filterPriceFrom: 0,
					filterPriceTo: 0,
					filterCategoryId: 0,
					page: 1,
					productsPerPage: 3,
					filterColorId: 0,
					productsData: null,
						/* 	productData будет у нас хранить информацию о запросе, который будет обрабатываться,
								конечно асинхронно, поэтому его нельзя хранить как вычисляемое свойство в разделе 
								computed, так как все вычисляемые совйства должны быть синхронными операциями */
					productsLoading: false,
					productsLoadingFailed: false,
			}
		},
		computed: {
			
			products(){
			
				//Обратить внимание, что используем такое же название для функции, что и общий список товаров...
				//...следовательно не нужно ничего менять в настройках шаблонов и директив v-bind и т.д. (см. выше)
				
				return this.productsData ? 
					this.productsData.items.map(product => {
						return {
							...product,
							image: product.image.file.url
						}
					}) 
					: [];
				
					/* 	возвращаем теперь список из состояния, в .items хранится массив объектов товаров. 
							поскольку состояние постоянно отрендеривает изменение информации в шаблон комопнента,
							то специально отслеживать изменение (возварщение ответа от "Промиса" не нужно --
							сам, как только будет готов, будет обновлен.
							
							Далее мы удостовериваемся, что получили список и немного меняем структуру объекта
							полученного с сервера просто для того, чтобы не переделывать структуру хранения
							данных, которую мы прописали до этого в компонентах и в хранилище
							
							*/
					
			},
			
			countProducts(){
			
				return this.productsData ? this.productsData.pagination.total : 0;
					/* 	Переделали вычисляемое свойство подсчёта кол-ва товаров на страницу,
							теперь обрабатываем ответ с сервера, который мы записали в раздел состояния
							компонента, обращаемся в подобъект pagination, в котором хранится информация */
				
			}
			
		},
		methods: {
			loadProducts(){
			
				/* 	это будет метод отправки запроса на сервер за спском товаров (используем ещё библиотеку axios!):
						в бибилиотеке axios в методе get() можно вторым аргументом мередавать объект с параметрами
						"квери-строки", то есть прошлая запись была бы слишком громоздкой, а так мы в свойстве "params"
						передаём список параметров для обработки запроса, в соответствии с документацией API данного проекта
						подставляем значения из фмльтра (крайние значения цены,  и пр.) из состояния, в результате в ответе
						мы получим уже обработанный (отфильтрованный список товаров) 
						
						Можно проверить в DevTools браузера, что запрос (строка адреса) составлен верно и дополнительные
						параметры подставлены в строку после знака "?"... */
						
				this.productsLoading = true;
				this.productsLoadingFailed = false;
				
				clearTimeout(this.loadProductsTimer);
						// очищаем таймер при запуске функции, а потом его ещё раз создаём и уже внутрь
						// мы помещаем наш запрос -- это для того, чтобы не получалось три запроса одновременно,
						// когда мы фильтруем сразу по нескольким параметрам (и цене, и категории и т.д.)
				
				this.loadProductsTimer = setTimeout(() => {
				
						// обратить внимание, что this.loadProductTimer -- это просто созданная нами переменная внутри компонента,
						// она не включена ни в какой раздел, сущесвует сама по себе, для удобства отключения таймера
					
					axios.get(API_BASE_URL + '/api/products', {
						params: {
							page: this.page,
							limit: this.productsPerPage,
							categoryId: this.filterCategoryId,
							minPrice: this.filterPriceFrom,
							maxPrice: this.filterPriceTo,
							colorId: this.filterColorId
						}
					})
					.then(response => this.productsData = response.data)
						/* 	тут мы возвращаемый promise записываем в переменную, хранимую в состоянии компонента,
								то есть в свойстве data()... */
					.catch(() => this.productsLoadingFailed = true)
					.then(() => this.productsLoading = false);
						/*	сначала именно ловим возможные ошибки с помощью метода .catch() и только
								после него прописывем дополнительный метод .then() со сбросом "прелоадера-спиннера",
								чтобы он обязательно был убран вне зависимости от результатов загрузки данных */
				
				}, 2000); // поставили ноль (?)
				
						/* 	Вообще получается хитрое использование таймер, у нас приходит получается (при фильтрации по трём параметрам)
								три запроса, они становятся в очередь, но поскольку мы очищаем таймер каждый раз в начале метода, то
								в итоге мы отправляем на сервер запрос с самыми крайними, свежими параметрами! :) */
				
			},
		},
		watch: {
			
			/*	в свойстве watch мы указываем те переменные из состояния, за которыми нужно следить, если
					в низ меняется значение, то внутри функции мы можем указать метод, который должен быть
					вызван, в данном случае это loadProducts(), чтобы подгружались те товары, которые должны быть
					на определённоый странице (свойство page в data()) */
			
			page(){
				this.loadProducts();
			},
			
			/* 	добавили ещё дополнительные свойства/переменные, изменения в которых необходимо отслеживать,
					так логика та же: как только значение меняется мы вызываем метод loadProducts(), которые составляет новый запрос
					исходя из изменившихся значений условий фильтрации списка товаров. */
					
			filterPriceFrom(){
				this.loadProducts();
			},
			filterPriceTo(){
				this.loadProducts();
			},
			filterCategoryId(){
				this.loadProducts();
			},
			filterColorId(){
				this.loadProducts();
			},
			
		},
		created(){
			/* 	created() -- это, как я понял функция по умолчанию доступная во Vue-компонентах, которая
					вызывается при создании (отрисовке?) компонента (или его загрузки?), тут получается,
					что мы при загурзке компонента вызываем метод loadProducts, чтобы получить информацию
					о списке товаров с сервера */
			this.loadProducts();
		},
		
	};

</script>