<template>
	<label class="form__label">
		<slot/>
			<!-- ВНИМАНИЕ --  это специальное поле для приёма информации в шаблон компонента -->
			<!-- из родительского компонента, в который мы будет импортировать данный шаблон -->
			<!-- как это реализовывается, смотри в комопненте OrderPage.vue -->
		<span class="form__value">{{ title }}</span>
		<span class="form__error" v-if="error">{{ error }}</span>
	</label>
</template>

<script>
	
	export default {
		props: ['title', 'error'],
			/* 	Здесь принимаем в качестве входящих параметров некие постоянные,
					которые мы будем вписывать в поле формы, в данном случае подразумевается,
					что это будет название поля формы из массива названий, а так же сообщение
					об ошибке, если она появится. */
	}
</script>