<template>
	<BaseFormField :title="title" :error="error">
		<!-- Вот, тут мы внутрь компонента вставляет html-элемент, который адеватно -->
		<!-- воспринимается из-за того, что внутри шаблона компонента BaseFormField -->
		<!-- есть специальный тег <slot/>, он позволяет менять некоторые нюансы разметки -->
		<!-- шаблона, если эти детали сложно как-то прописать по-другому... -->
		<input 
			v-model="dataValue"
			class="form__input" 
			:type="type" 
			name="name" 
			:placeholder="placeholder"
			>
	</BaseFormField>
</template>


<script>
	import formFieldMixin from '@/mixins/formFieldMixin';
		// импортировали "mixin"
	export default {
		props: {
			type: {
				default: 'text'
					/* Мы можем передавать входящие параметры и в виде объекта,
						 а не строки, тогда мы можем указывать дополнительные
						 варианты настроек, в данном случае -- это прописано
						 дефолтное значение параметра, которое мы подставляем
						 в атрибут v-bind:type (!) */
			}
		},
		mixins: [formFieldMixin],
			/*	Свойство (раздел) "mixins" в экземпляре (компоненте) Vue -- это
					то же самое, что и "components", но для js-файлов, которые прямо замещают
					какой-то набор скриптов или структуру объекта экземпляра Vue, синтаксис
					подключения аналогичен подключению vue-компонентов, кроме одной детали --
					нужно перечислять миксины не в объекте, а в массиве (квадратные скобочки!)... 
					Одно из преимущество миксинов -- это возможность смешивать их с другими настройками
					экземпляра компонента Vue, так, определённые props выше будут совмещены
					с props внутри данного миксина.*/
	}
</script>