<template>
<main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item" >
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item" >
          <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item" >
          <router-link class="breadcrumbs__link" :to="{name: 'order'}">
            Оформление заказа
          </router-link>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
       {{ productsCounter }}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
				<!-- при событии submit вызываем метод "order", который отправляет данные на сервер -->
        <div class="cart__field">
          <div class="cart__data">
					
						<BaseFormText 
							v-model="formData.name" 
							:error="formError.name" 
							title="ФИО" 
							placeholder="Введите ваше полное имя"/>
					
						<BaseFormText 
							v-model="formData.address" 
							:error="formError.address" 
							title="Адрес доставки" 
							placeholder="Введите ваш адрес"/>
					
						<BaseFormText 
							v-model="formData.phone" 
							:error="formError.phone" 
							title="Телефон" 
							placeholder="Введите ваш контактный телефон"/>
					
						<BaseFormText 
							v-model="formData.email" 
							:error="formError.email" 
							title="Email" 
							placeholder="Введите ваш Email"/>
						
						<BaseFormTextarea 
							v-model="formData.comment" 
							:error="formError.comment" 
							placeholder="Ваши пожелания"/>
						
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="item in orderItems" :key="item.id">
              <h3>{{ item.product.title }}</h3>
              <b>{{ item.product.price * item.quantity | numberFormat }} ₽</b>
              <span> Артикул: {{ item.product.id}} </span>
            </li>
          </ul>
          
          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b> {{productsCounter}}</b> на сумму <b>{{ total_price | numberFormat }} ₽</b></p>
          </div>

          <button class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <!-- Отображаем блок, если при отправке формы поймали глобальную ошибку -->
					<!-- которая записывается в переменную formErrorMessage... -->
					<h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
	
	import BaseFormText from '@/components/BaseFormText';
	import BaseFormTextarea from '@/components/BaseFormTextarea';
	import axios from 'axios';
	import { API_BASE_URL } from '../config';
	import numberFormat from '@/helpers/numberFormat';
	import productsCounter from '@/helpers/productsCounter';
	import { mapGetters } from 'vuex';
	
	export default {
		components: {BaseFormText, BaseFormTextarea},
		data(){
			return {
				formData: {},
					/* 	Сюда мы будем помещать пары "ключ: значение" (на вроде того: 
							"formData.[имя нового ключа]"), ооответствующие 
							полям формы и введённым в них значениями/дфанным */
				formError: {},
					/*	Аналогичный объект, но только для ошибок... */
				formErrorMessage: '',
					/* переменная для вывод сообщений о глобльарных ошибках */
			}
		},
		filters: {
			numberFormat,
		},
		computed: {
			...mapGetters({total_price: 'cartTotalPrice'}),
			orderItems(){
				return this.$store.state.cartProductsData;
			},
			productsCounter,
		},
		methods: {
			order() {
					
				this.formError = {};	
					// предварительно очищаем объект с ошибками, чтобы в случае успешного запроса
					// какие-то старые ошибки не оставались висеть в интерфейсе :)
				this.formErrorMessage = '';
					// аналогично с хранением текста глобальных ошибок...
				
				axios	
					.post(
						API_BASE_URL + '/api/orders', 
						{
							...this.formData
						},
						{
							params: {
								userAccessKey: this.$store.state.userAccessKey
							}
						}
					)
					.then((response) => {
						this.$store.commit('resetCart');
							// если запрос был успешным, то вызываем функцию-мутацию в хранилище
							// для очистки корзины (и интерфейса, и хранилища)
						this.$store.commit('updateOrderInfo', response.data);
							/* Передаём в хранилище Vuex данные ответа, чтобы потом использовать
								 их в других компонентах (первый аргумент у метода .commit() -- этого
								 название вызываемой функции-мутации из хранилища, второй -- 
								 данные, которые в неё передаются) */
						this.$router.push({name: 'orderInfo', params: {id: response.data.id}});
							/* с помощью метода .push() "отправляем" пользователя
								 на страницу оформленного заказа (см. адреса в router/index.js),
								 окончательный адрес которой включает id заказа (параметры для
								 динамического адреса передаём второым ключём в объект-аргумент
								 метода .push() */
							
					})
					.catch(error => {
						this.formError = error.response.data.error.request || {};
							/*Список ошибок нам возвращается с сервер (если ошибки есть), поэтому
								нам нужно их записать к нам в объект для хранения ошибок, чтобы они
								отобразились в форме шаблона для того, чтобы пользователь их видел...
								Обратить внимание, как можно избежать конструкций if() ... else
								при записи данных, логический оператор "или" передаст в переменную
								второе значение, только если первое (до знакак "||") будет недействительно
								*/
						this.formErrorMessage = error.response.data.error.message;
							/*Тут, если такое есть, записываем сообщение глобальной ошибки в нашу
								специально отведённую для этого переменную, обращаемся для получения
								текста ошибки по структуре передаваемого с сервера ответа. 
								*/
					});
			},
		},
	}
</script>