<template>
	<li class="cart__item product">
		<div class="product__pic">
			<img :src="item.product.image" width="120" height="120" :alt="item.product.title">
		</div>
		<h3 class="product__title">
			{{ item.product.title }}
		</h3>
		<span class="product__code">
			Артикул: {{ item.product.id }}
		</span>

		<div class="product__counter form__counter">
			<button type="button" aria-label="Убрать один товар" v-on:click="amountMinus">
				<svg width="10" height="10" fill="currentColor">
					<use xlink:href="#icon-minus"></use>
				</svg>
			</button>

			<input type="text" v-model.number="amount" name="count">

			<button type="button" aria-label="Добавить один товар" v-on:click="amountPlus">
				<svg width="10" height="10" fill="currentColor">
					<use xlink:href="#icon-plus"></use>
				</svg>
			</button>
		</div>

		<b class="product__price">
			{{ (item.amount * item.product.price) | numberFormat }} ₽
				<!-- Запомнить, как вставлять математические выражения внутрь {{}}, просто заключить в скобки. -->
				<!-- "| numberFormat" -- это специальная запись для применения функции форматирования чисел из библиотеки -->
		</b>

		<button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click.prevent="deleteApiProduct(item.productId)">
			<svg width="20" height="20" fill="currentColor">
				<use xlink:href="#icon-close"></use>
			</svg>
		</button>
	</li>
</template>

<script>
	import numberFormat from '@/helpers/numberFormat';
		// импортировали фукнцию для форматирования чисел
	import {mapMutations} from 'vuex';
		// импортировали специальную фукнцию из библиотеки, чтобы отслеживать историю обращения к функция-мутациям
	import {mapActions} from 'vuex';
		// импорт для удобство использования функций-действий в качестве методов компонента
	export default {
		props: ['item'],
		filters: {numberFormat},
			/*
					свойство/раздел "filters" -- для функций, которые как-то взаимодействуют с выводом данных,
					но не в каком-то глобальном плане, а более в "косметическом"
			*/
		computed: {
			amount: {
				get(){
					return this.item.amount;
				},
				set(value){
					this.$store.dispatch('updateCartProductAmount', {productId: this.item.productId, amount: value});
						/*
							Здесь по аналогии с сеттером класса "value" -- это новое полученное значение, подставляем его
							в передаваемое в геттер хранилища amount (см. store/index.js). К хранилищу обращаемся 
							через $store.commit(['имя фукнции-мутации в хранилище'], [передаваемый объект или др. переменная])
							*/
						/*
							Теперь только мы обращаемся в хранилище с методом .dispatch(), а не .commit(), чтобы обратиться
							именно к разделу "actions", а не "mutations", а название у функцию у нас было тоже самое, так что
							ничего больше менять не нужно...
							*/
				}
			},
		},
		methods: {
			...mapMutations({deleteProduct: 'deleteCartProduct'}),
			...mapActions(['deleteApiProduct']),
				/* 
					Помним, что переадавая в такую функцию объект мы по сути оптимизируем код: мы указываем в ключе название
					метода,к  которому обращаемся из html-шаблона компонента, а в значении мы указываем название функции-мутации,
					получается мы их связываем без дополнительных описаний логики работы метода, его теперь можно удалить
				*/
			amountPlus: function() {
				this.item.amount++;
				this.$store.dispatch('updateCartProductAmount', {productId: this.item.productId, amount: this.item.amount});
			},
			amountMinus: function() {
				this.item.amount--;
				this.$store.dispatch('updateCartProductAmount', {productId: this.item.productId, amount: this.item.amount});
			},
		},
	}
</script>