<script>

	import CartIndicator from '@/components/CartIndicator'
	import { mapActions, mapMutations } from 'vuex';
		/* 	mapActions -- это нативная фукнция для бибилиотеки Vuex, которая как бы импортирует функции из раздела "actions"
				в глобальном хранилище в раздел "methods" текущего компонента так, чтобы мы могли обращаться к ним уже не через
				"this.$store.[то-то и то-то]", а просто "this.[имя функции из раздела actions в $store]" -- см. раздел methods (!) */

	export default {
		components: {CartIndicator},
		methods: {
			...mapActions(['loadCart']),
					// теперь ссылаемся на эту функцию а в ургументах в виде массива можем перечислить нужные нам функции
					// из раздела "actions" в глобальном хранилище, которое инициализируем с помощью библиотеки Vuex
			...mapMutations(['updateUserAccessKey']),
					// аналогично и с mapMutations...
		},
		created(){

			// created() -- нативная функция срабатывающая при загрузке компонента ( в данном случае головного компонента Vue)

			const userAccessKey = localStorage.getItem('userAccessKey');
			if (userAccessKey) {
				this.updateUserAccessKey(userAccessKey);
				/* Здесь мы, если в локальном хранилище браузера был записан ключ, выгружаем ключ и обновляем
					 в хранилище vuex состояние userAccessKey */
			}
			
			this.loadCart();
			
			/*	Здесь мы бы обращались к хранилищу и через метод .dispatch() (this.$store.dispatch('loadCart')), то есть
						таки образом вызывали бы функцию-действие из раздела "actions", в котором моно хранить асинхронные операции, запускающие в свою очередь
						синхронные операции, хранимые в глоблальном хрнилище в разделе "mutations".
						Но поскольку мы используем импортировнную функцию mapActions() в свойстве "methods" данного компонента,
						мы можем теперь просто обращаться к loadCart(), как к методу данного компонента!*/
		},
	};

</script>
