// Импортируем Vue и библиотеку Vuex
import Vue from 'vue';
import Vuex from 'vuex';
// импортируем библиотеку axios для работы с запросами на сервер:
import axios from 'axios';
// импортируем постоянную базового домена:
import {API_BASE_URL} from '../config';

// Команда использования библиотеки: 
Vue.use(Vuex);

/* 
(!)		Основное преимущество этой библиотеки -- это обособленное хранение всех изменяемых данных,
(!)		а так же удобство в том, что мы можем менять структуру хранения данных и принципы работы с ними
(!)		обособленно, независимо от компонентов из которых мы получаем данные!
*/

export default new Vuex.Store({
//РАЗДЕЛ СОСТОЯНИЯ (ДАННЫЕ):
  state: {
		cartProducts: [
			// {productId: 1, amount: 2,}
		],
		userAccessKey: null,
			// переменная для хранения ключа пользователя (хранилище как раз подходит для хранения таких данных)
		cartProductsData: [],
		orderInfo: null,
	},
// РАЗДЕЛ "МУТАЦИЙ" -- ДЛЯ ПРИЁМА ДАННЫХ И ИХ ИЗМЕНЕНИЯ (то есть данных из раздела state);
// функции в разделе должны быть строго синхронными, поэтому записывать сюда обращения с серверными данными нельзя:
	mutations: {
		updateOrderInfo(state, orderInfo) {
			/* функция для передачи данных о заказе со страницы оформления заказан
				 на страницу информации о заказе (OrderInfoPage) */
			state.orderInfo = orderInfo;
		},
		resetCart(state) {
			state.cartProducts = [];
			state.cartProductsData = [];
				/* Очищаем все данные по выбранным товарам, будем использовать
					 эту функцию-мутацию для обновления данных в корзине при 
					 удачной отправке формы заказан */
		},
		updateCartProductAmount(state, {productId, amount}) {
			// Обратить внимание как можно передавать второй аргумент в фукнцию-мутацию: в виде объекта с какбы "под-аргументами"!
			const item = state.cartProducts.find(item => item.productId === productId);
			if (item) {
				item.amount = amount; 
				/*Здесь мы свойству amount у найденного объекта присваиваем занчение передаваемого второго "под-аргумента" 
					внимание, к нему мы тоже можем обращаться просто по имени аругмента, то есть "amount" (никаких накладок!),
					так же выше обращаемся просто productId, а не payload.productId... */
				if (item.amount < 1) {
					state.cartProducts = state.cartProducts.filter(item => item.productId !==productId);
				}
			}
		},
		deleteCartProduct(state, productId) {
			// Обратить внимание тут никаких payload'ов (может быть потому что передаваемый аргумент представляет собой не объект или массив?)
			state.cartProducts = state.cartProducts.filter(item => item.productId !== productId);
				// Отфильтровываем теперь массив из свойство state отбрасывая объект с товаром, у которого id c переданным из компонента CartItem
		},
		updateUserAccessKey(state, accessKey) {
			// это мутация для обновления значения ключа-иентифиактора пользователя, для згрузки соответствующей корзины
			state.userAccessKey = accessKey;
		},
		updateCartProductsData(state, items) {
			// это мутация для обновления списка товаров, который должен отобразиться в корзине конкрентного пользователя
			state.cartProductsData = items;
			console.log(`Мутация updateCartProductData запущена, получен список : ${state.cartProductsData}; записаны в хранилищей в переменную cartProductsData.`);
		},
		syncCartProducts(state) {
			// это мутация для записи информации товаров из корзины в нужно формате для дальнейшей работы:
			state.cartProducts = state.cartProductsData.map(item => {
				return {
					productId: item.product.id,
					amount: item.quantity
				}
			});
		}
	},
// РАЗДЕЛ ФУНКЦИЙ (ТИПА ВЫЧИСЛЯЕМЫX СВОЙСТВ):
	getters: {
		cartDetailProducts(state) {
			return state.cartProducts.map(item => {
				const product = state.cartProductsData.find(p => p.product.id === item.productId).product;
					// ищем в массиве переданных с сервера товаров позиции по id, выбираем и потом обращаемся в свойство .product
					// (смотри структуру передаваемых с сервера данных в Vue DevTools в браузере)
				return {
					...item,
					product: {
						...product,
						image: product.image.file.url,
						// записываем данные в возвращаемый объект, причём, чтобы у нас отображались иллюстрации
						// мы перезаписываем объект product, добавляя/перезаписывая значение свойства image,
						// обращаясь опять же к самом объекту -- по сути просто упрощаем запись (?)
					},
				}
			});
		},
		cartTotalPrice(state, getters) {
			// тут примечательно, что второй аргумент -- это обращение к геттерам хранилища:
			return getters.cartDetailProducts.reduce((acc, item) => (item.product.price * item.amount) + acc, 0);
			// принцип такой, что указав getters в аргументах, мы теперь внутри новой функции можем обращаться к другим функциями
			// внутри объекта getters данного хранилища
		},
	},
// РАЗДЕЛ ФУНКЦИЙ "ДЕЙСТВИЯ" ДЛЯ ОПИСАНИЯ В Т.Ч. И АСИНХРОННЫХ ОПЕРАЦИЙ:
	actions: {
		/* 	В разделе "actions" мы можем обращаться к самому хранилищу используя метод .commit(), которые до этого мы
				использовали для обращения к хранилищу из компонентов Vue, а так же вызываеть уже другие методы-мутции, 
				которые будут запускать обработку данных, хранимых в разделе state, следовательно
				поэтому здесь и можно обрабатывать асинхронно поступающие данные */
		/*	Все асинхронные операции должны возвращать "промисы"! Не забываем ставить return перед 
				описанием наших асинхронных функций внутри данного раздела! */
		loadOrderInfo(context, orderId) {
			return axios
				.get(API_BASE_URL + '/api/orders/' + orderId, {
					params: {
						userAccessKey: context.state.userAccessKey
					}
				})
				.then(response => {
					context.commit('updateOrderInfo', response.data);
						/* Обновляем информацию */
				});
		},
		loadCart(context) {
			return axios
				.get(API_BASE_URL + '/api/baskets', {
					params: {userAccessKey: context.state.userAccessKey}
						/* этот объект -- третий аргумент в метод .get() библиотеки axios в удобной фоме
							 помогает добавлять к строке адреса квери параметры, которые нам нужны для
							 работы с сервером; в данном примере мы отправляем в конце строки ключ пользвотеля,
							 если он есть */
				})
				.then((response) => {
					if (!context.state.userAccessKey){
						localStorage.setItem('userAccessKey', response.data.user.accessKey);
							/* так как хранилище vuex у нас очищается при закрытии вкладки в браузере, то нам нужно
								 для страховки сохранять на время сессии уникальный ключ пользователя в localStorage браузера?
								 выполняется, если до этого ключ не был получен*/
						context.commit('updateUserAccessKey', response.data.user.accessKey);				
					}
					context.commit('updateCartProductsData', response.data.items);
						/*	здесь мы вызываем две мутации для обновления данных в состоянии хранилища, используя синтаксис
								метода .commit(), а к объекту хранилища мы обращаемся через кодовый аргумент "context" */
					context.commit('syncCartProducts');
						/* 	дополнительно, для удобства дальнейшей работы с данными мы переводим их в формат, который
								определили до этого в переменной сосотояния глоабльного хранилища (в cartProducts), мы
								вызываем функцию мутацию */
				});
		},
		addProductToCart(context, {productId, amount}){
			// ВНИМАНИЕ! нижеследующая обертка функции axios в .then() после "промиса с таймаутом для тестирования!"
			return (new Promise(resolve => setTimeout(resolve, 2000)))
				.then(() => {
					return axios.post(API_BASE_URL + '/api/baskets/products', {
							// метод .post() аналогичен запросу POST, вторым аргументом передаём данные в нужно формате
							// нужный формат смотри по ссылке API_BASE_URL... 
							productId: productId,
							quantity: amount
						}, {
							params: {
								userAccessKey: context.state.userAccessKey
									// третьим аргументом передаём дополнительные квери-параметры в строку запроса,
									// в данном случае индивидуальный ключ пользователя....
							}	
						})
						.then(response => {
							console.log(response);
							context.commit('updateCartProductsData', response.data.items);
							context.commit('syncCartProducts');
								/* аналогично другой функции-действию мы после получения ответа от сервера
									 вызываем функции-мутации для обновления состояния данных в разделе "state" 
									 нашего глобпльного хранилища Vuex */
						});
				});
			
		},
		updateCartProductAmount(context, {productId, amount}) {
			context.commit('updateCartProductAmount', {productId, amount});
				// вызываем мутацию, чтобы обновить показатель количества товара, это для того, чтобы для пользователя
				// представление интерфейса отражало изменение мгновенно, а вот изменения на сервере (которые требуют
				// ремени) мы отправляем как бы фоном
			if (amount === '') {
				return;
			}
			return axios
						.put(API_BASE_URL + '/api/baskets/products', {
							// метод .put() аналогичен запросу POST, вторым аргументом передаём данные в нужно формате
							// нужный формат смотри по ссылке API_BASE_URL... 
							productId: productId,
							quantity: amount
						}, {
							params: {
								userAccessKey: context.state.userAccessKey
									// третьим аргументом передаём дополнительные квери-параметры в строку запроса,
									// в данном случае индивидуальный ключ пользователя....
							}	
						})
						.then(response => {
							context.commit('updateCartProductsData', response.data.items);
						})
						.catch(() => {
							context.commit('syncCartProducts');
								/* в случае с изменением количества, мы синхронизируем данные только
									 в случе ошибки при выполнении промиса, чтобы "откатиться" к предыдущей версии
									 изменений, когда всё было в нужном формате */
						});
						
		},
		deleteApiProduct(context, productId) {
			// сначада удаляем позицию товара из внешнего представления:
			context.commit('deleteCartProduct', productId);
			// "фоном" отправляем запрос об удалении на сервер
			return axios
				.delete(API_BASE_URL + '/api/baskets/products', {
						params: {userAccessKey: context.state.userAccessKey},
						data: {productId} 
							/* (!) 	в этой библиотеке (axios) при использовании метода .delete() нужно передавать всё --
											и данные адресной строки, и данные для сервера в одном объекте, как один сложный аргумент,
											иначе не будет работать (странно, что в остальных случаях нужно было писать те же самые
											пары люч-значения для успешного запроса, а тут мы пишем не productId: productId, а data: productId) */
				})
				.then(response => {
					console.log(response);
					context.commit('updateCartProductsData', response.data.items);
				})
				// если произошла ошибка при запросе на удаление (то есть на сервере не удалили позицию),
				// то возвращаем в интерфейс пропавший товар:
				.catch(() => context.commit('syncCartProducts'));
		},
		
		
	}
});
