<template>

    <ul class="catalog__pagination pagination">
      <li class="pagination__item">
        <a @click="$event.preventDefault(); paginate(page - 1)" class="pagination__link pagination__link--arrow" :class="{ 'pagination__link--disabled' : page === 1 }" aria-label="Предыдущая страница">
          <svg width="8" height="14" fill="currentColor">
            <use xlink:href="#icon-arrow-left"></use>
          </svg>
        </a>
      </li>
      <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
				<!-- Обратить внимание, что v-for может хорошо распознавать числа (в данном случае -->
				<!-- возвращаемое значение функции/метода pages -- смотри ниже в разделе computed) -->
				<!-- так что создаст столько страничек, сколько обозначает число -->
        <a href="#" @click="$event.preventDefault(); paginate(pageNumber)" class="pagination__link" :class="{'pagination__link--current': pageNumber === page }">
					<!-- здесь конструкция :class="{[значение] : [условие его применения]}, то есть класс применится при совпадении номеров -->
          <!-- при вызове метода мы его записываем с аргументами (paginate(pageNumber))? -->
          <!-- базовые методы вызываются $[name of js-thing].[method name]? -->
					{{ pageNumber}}
        </a>
      </li>
      <li class="pagination__item">
        <a @click="$event.preventDefault(); paginate(page + 1)" class="pagination__link pagination__link--arrow" :class="{ 'pagination__link--disabled' : page === pages }" aria-label="Следующая страница">
          <svg width="8" height="14" fill="currentColor">
            <use xlink:href="#icon-arrow-right"></use>
          </svg>
        </a>
      </li>
    </ul>

</template>

<script>
	export default {
		model: {
			prop: 'page',
			event: 'paginate',
		},
		props: ['page', 'count', 'perPage'],
			// три переменные будем принимать: номер страницы, общее количество элементов, количество позиций на странице
			// обрати внимание, что 'perPage' в camelCase, но в шаблоне разметки vue ты будешь его вызывать как ':per-page',
			// то есть в стиле "кебаб" (!)
		computed: {
			pages(){
				return Math.ceil(this.count / this.perPage);
			}
		},
		methods: {
			paginate(page){
				if (page < 1) return;
				if (page > this.pages) return;
				this.$emit('paginate', page);
					// метод $emit -- это по сути обработчик своего события,описанного в свойстве "model"
			}
		}
	}
</script>