<template>
	<main class="content container" v-if="productLoading"> Загрузка товара... </main>
	<main class="content container" v-else-if="!productData"> Не удалось загрузить страницу </main>
	<main class="content container" v-else>
		<!-- с помощью директивы v-if мы указываем условия, когда этот шаблон должен отрисоваться: -->
		<!-- только тогда, когда будут получены данные с сервера, место для которых зарезервировано -->
		<!-- нами в переменной productData, хранимой в состоянии приложения (в свойстве data()) -->
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
						<!-- Компонент "router-link" из библиотеки "vue-router" заменяет тег <a>, а v-bind:to обращается -->
						<!-- к константе routes декларированной в файле index.js, а конкретно к обхекту, содержащему путь/адрес, -->\
						<!-- но в качестве идентификаторп используем "name", чтобы не зависеть от возможных изменений адреса страницы -->
						<!-- то есть, чтобы мы могли спокойно менять свойство "path" в  объекте пути -->
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            {{ category.title }}
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            {{ product.title }}
          </a>
        </li>
      </ul>
    </div>

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img width="570" height="570" :src="product.image.file.url" :alt="product.title">
						<!-- подправил значение v-bind:src соответственно структуре передаваемых с сервера данных о товаре... -->
					</div>
      </div>

      <div class="item__info">
        <span class="item__code">Артикул: {{ product.id }} </span>
        <h2 class="item__title">
          {{ product.title }}
        </h2>
        <div class="item__form">
          <form class="form" action="#" method="POST" @submit.prevent="addToCart">
						<!-- вешаем обработчик подтверждения формы (при нажатии на кнопку внутри), который вызывает метод 'addToCart' -->
            <b class="item__price">
              {{ product.price | numberFormat }} ₽
								<!-- Обрати внимание, как применяется метод из свойства "filters" из раздела "script"... -->
								<!-- напротив какого-то значения ставим прямую веритклаьную черту и после неё пишем название метода без скобок! -->
            </b>

            <fieldset class="form__block">
              <legend class="form__legend">Цвет:</legend>
              <ul class="colors">
                <li class="colors__item" v-for="color in colors" :key="color.id">
                  <label class="colors__label">
                    <input class="colors__radio sr-only" type="radio" name="color-item" :title="color.code" :value="color.code" checked="">
                    <span class="colors__value" :style="{backgroundColor: color.code}">
                    </span>
                  </label>
                </li>
              </ul>
            </fieldset>

            <fieldset class="form__block">
              <legend class="form__legend">Объемб в ГБ:</legend>

              <ul class="sizes sizes--primery">
                <li class="sizes__item">
                  <label class="sizes__label">
                    <input class="sizes__radio sr-only" type="radio" name="sizes-item" value="32">
                    <span class="sizes__value">
                      32gb
                    </span>
                  </label>
                </li>
                <li class="sizes__item">
                  <label class="sizes__label">
                    <input class="sizes__radio sr-only" type="radio" name="sizes-item" value="64">
                    <span class="sizes__value">
                      64gb
                    </span>
                  </label>
                </li>
                <li class="sizes__item">
                  <label class="sizes__label">
                    <input class="sizes__radio sr-only" type="radio" name="sizes-item" value="128" checked="">
                    <span class="sizes__value">
                      128gb
                    </span>
                  </label>
                </li>
              </ul>
            </fieldset>

            <div class="item__row">
              <div class="form__counter">
							
                <button type="button" aria-label="Убрать один товар" v-if="productAmount > 1" @click="productAmount--">
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-minus"></use>
                  </svg>
                </button>
                
								<button type="button" aria-label="Убрать один товар" v-else disabled>
								  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-minus"></use>
                  </svg>
                </button>

                <input type="text" v-model.number="productAmount">
									<!-- v-model.number="[что-то]" будет переводить строку в число, чтобы, если что было математическое сложение, а не конкатеннация -->

                <button type="button" aria-label="Добавить один товар" @click="productAmount++">
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-plus"></use>
                  </svg>
                </button>
              </div>

              <button class="button button--primery" type="submit" v-bind:disabled="productAddSending">
								<!-- обратить внимание, как элегантно мы с помощью Vue управлям занчениями атрибутов: мы просто -->
								<!-- подставляем значение переменной из состояния (true/false), чтобы атрибут disabled соответственно -->
								<!-- либо активировался, либо убирался. -->
                В корзину
              </button>
            </div>
						
						<div v-show="productAdded"> Товар добавлен в корзину! </div>
						<div v-show="productAddSending"> Товар добавляется в корзину... </div>
							<!-- показываем сообщения о статусе процесса отправки в зависимости от "активной" переменной -->
						
          </form>
        </div>
      </div>

      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item">
            <a class="tabs__link tabs__link--current">
              Описание
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
              Характеристики
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
              Гарантия
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
              Оплата и доставка
            </a>
          </li>
        </ul>

        <div class="item__content">
          <p>
            Навигация GPS, ГЛОНАСС, BEIDOU Galileo и QZSS<br>
            Синхронизация со смартфоном<br>
            Связь по Bluetooth Smart, ANT+ и Wi-Fi<br>
            Поддержка сторонних приложений<br>
          </p>
  
          <a href="#">
            Все характеристики
          </a>
  
          <h3>Что это?</h3>
  
          <p>
            Wahoo ELEMNT BOLT GPS – это велокомпьютер, который позволяет оптимизировать свои велотренировки, сделав их максимально эффективными. Wahoo ELEMNT BOLT GPS синхронизируется с датчиками по ANT+, объединяя полученную с них информацию. Данные отображаются на дисплее, а также сохраняются на смартфоне. При этом на мобильное устройство можно установить как фирменное приложение, так и различные приложения сторонних разработчиков. Велокомпьютер точно отслеживает местоположение, принимая сигнал с целого комплекса спутников. Эта информация позволяет смотреть уже преодоленные маршруты и планировать новые велопрогулки.
          </p>
  
          <h3>Дизайн</h3>
  
          <p>
            Велокомпьютер Wahoo ELEMNT BOLT очень компактный. Размеры устройства составляют всего 74,6 x 47,3 x 22,1 мм. что не превышает габариты смартфона. Корпус гаджета выполнен из черного пластика. На обращенной к пользователю стороне расположен дисплей диагональю 56 мм. На дисплей выводятся координаты и скорость, а также полученная со смартфона и синхронизированных датчиков информация: интенсивность, скорость вращения педалей, пульс и т.д. (датчики не входят в комплект поставки). Корпус велокомпьютера имеет степень защиты от влаги IPX7. Это означает, что устройство не боится пыли, а также выдерживает кратковременное (до 30 минут) погружение в воду на глубину не более 1 метра.
          </p>
        </div>
      </div>
    </section>
  </main>
</template>
<script>

	import gotoPage from '@/helpers/gotoPage';
		/* Импортируем сюда метод для определения, какой подгружать компонент с разметкой контента */
	import numberFormat from '@/helpers/numberFormat';
	import axios from 'axios';
		/* Библиотека для управщения работы с запросами на сервер */
	import {API_BASE_URL} from '../config';
	import { mapActions } from 'vuex';
		/* нативная функция, которая позволяет импортировать функции-действия из Vuex в methods текущего Vue-компонента */
	
	export default {
		data(){
			return {
				productAmount: 1,
				productData: null,
				productLoading: false,
				productLoadingFailed: false,
				productAdded: false,
				productAddSending: false,
			};
		},
		computed: {
			product(){
				return this.productData;
					/*Вовзращаем полученные с сервера данные хранящиеся в состоянии компонента, которые хоть и подгружаеются
						асинхронно, но будут сразу отрисованы, как только будут получены */
			},
			category(){
				return this.productData.category;
			},
			colors(){
				return this.productData.colors;
			},
		},
		methods: {
			...mapActions(['addProductToCart']),
			gotoPage,
			addToCart() {
				this.productAdded = false;
				this.productAddSending = true;
				this.addProductToCart({productId: this.product.id, amount: this.productAmount})
					/* поскольку мы импортировали mapActions и указали в аргументах addProductToCart, то для того, чтобы
						 передать данные в функцию-действие в зранилище Vue, в раздел actions, можно просто вызвать
						 как метод без обращение this.$store[...] */
					.then(() => {
						this.productAdded = true;
						this.productAddSending = false;
							/*	Далее: поскольку вызываемый метод (какбы импортированный с помощью mapActions() из хранилища) 
									функции-действия является асинхронным, то мы можем использовать метод .then(), для того чтобы
									последовательно менять какую-то информацию без всяких if() ... else (!) ^^ */
					});
			},
			loadProduct(){
				this.productLoading = true;
				this.productLoadingFailed = false;
				axios.get(API_BASE_URL + '/api/products/' + this.$route.params.id)
					/* 	тут нужно обратить внимание как мы состоавляем адрес запроса: в конце строки нам нужно вставить
							динамическое окончание в виде id товара, обращаемся (судя по всему) к созданному адресу страницы
							товара (сам файл -- это компонент, а подставляем мы адрес с помощью библиотеки Vue-router), вот
							мы как бы обращаемся к этому объекту, в котором хранится информация и об id-товара (так как номер
							мы подставляем у каждого товара свой) */
					.then(response => this.productData = response.data)
					.catch(() => this.productLoadingFailed = true)
					.then(() => this.productLoading = false);
			},
			
		},
		filters: {
			numberFormat,
				/* Свойство объекта Vue "filters" похоже по своему назначению на свойство "methods", то есть
				хранит какие-то методы/функции для работы с другими совйствами, но обычно, как я понял, в данном разделе
				перечисляются функции, которые имеют менее глобальное значение и относятся к каким-то мелким деталям
				*/
		},
		watch: {
			/* В свойстве объекта экземпляра Vue будем наблюдать за адресной строкой в браузере, если
				 она поменяет значение, то будет запускать перезагрузку всего компонента ProductPage.vue.
				 Нужно обратить внимане на то, теперь мы строку используем как ключ в объекте, значение у которой
				 тоже объект, в котором мы описываем два условия вызова метда loadProduct -- это изменение адресной строки
				 и так же при перезагрузке компонента, так нам не нужно прописывать дополнительный "хук" created(),
				 как у нас было сделано до этого...*/
			'$route.params.id':{
				handler(){
					this.loadProduct();
				},
				immediate: true,
			},
		},
	}
</script>