  <ul class="catalog__list">
    <ProductItem :product="product" v-for="product in products" v-bind:key="product.id"></ProductItem>
			
			<!-- Итак здесь мы (см секцию js) приняли список продуктов из центрального файла .vue (см. 'props: ['products'],') -->
			<!-- А импорт компонента в компонент осуществляем через 'import ComponentName @/file-adress', теперь... -->
			<!-- Шаблон ProductItem распознается верно, а директива "v-for", видимо, передаст текущее в цикле наименование товара... -->
			<!-- ...в шаблон "ProductItem". -->
			
  </ul>
	
</template>

<script>
import ProductItem from '@/components/ProductItem.vue';
  export default {
		components: { ProductItem },
    props: ['products'],
  }

