export default function productsCounter(){
	// вычисляемое свойство для строки в заголовке (сколько всего позиций в корзине)
	let string = '';
	const numberString = String(this.$store.state.cartProducts.length);
	const lastDigit = Number(numberString[numberString.length - 1]);
	const beforeLastDigit = Number(numberString[numberString.length - 2]);
	if ((lastDigit > 1 && lastDigit < 5) && beforeLastDigit !== 1) string = 'товара';
	else if (lastDigit === 1 && beforeLastDigit !== 1) string = 'товар';
	else string = 'товаров';
	return numberString + ' ' + string;
}