import eventBus from '@/eventBus';

export default function gotoPage(pageName, pageParams){
	eventBus.$emit('gotoPage', pageName, pageParams);
		/* тут, в этом методе, мы и используем $emit(), в которой передаем название события, а так же два параметра, которые
		принимаем из аргументов метода (такое же название метода взято для удобства) и регистрируем это к глоабльном объекте,
		Vue, импортируемого под навзанием eventBus из файла eventBus.js... теперь, мы можем обратиться к этим параметрам
		из любого другого файла/компонента, как я понимаю... */
}