<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ productsCounter }}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
						<CartItem v-for="item in products_in_cart" :key="item.productId" :item="item"></CartItem>
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span> {{ total_price | numberFormat }} ₽ </span>
          </p>

          <router-link tag="button" :to="{name: 'order'}" class="cart__button button button--primery" type="submit">
            <!-- Помим, что v-bind:to -- это атрибут для указания адреса, так мы указываем, что нужно... -->
						<!-- переместиться по адресу, указанному в массиве "routes" в "router/inex.js" -->
						Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
	import numberFormat from '@/helpers/numberFormat';
		// импортировали фукнцию для форматирования чисел
	import  CartItem  from '@/components/CartItem';
	import { mapGetters } from 'vuex';
		// импортировали функцию из библиотеки "vuex" для дальнейшей работы с getter'ами...
	import productsCounter from '@/helpers/productsCounter';
	export default {
		data() {
			return {
			}
		},
		components: { CartItem },
		computed: {
			...mapGetters({products_in_cart: 'cartDetailProducts', total_price: 'cartTotalPrice'}),
				/*подставляем эту (импортированную из vuex функцию как свойство объекта "computed" с вычисляемыми свойствами)
					если в качестве аргумента передать объект в котром ключи будут названиями вычисляемых свойств, а значениями
					будуте функции-геттеры из хранилища, то это будет идентично рпедыдущей записи, но гораздо экномичнее 
				*/
				/* Ещё удобно через запятую перечислять все геттеры для добавляения их в список вычисляемых свойств шаблона -- удобно */
			productsCounter,
		},
		filters: {numberFormat},
			/*
					свойство/раздел "filters" -- для функций, которые как-то взаимодействуют с выводом данных,
					но не в каком-то глобальном плане, а более в "косметическом"
			*/
	}
	
</script>