/* 	Миксины -- это по сути именно "скопировать-вставить", этакией
		шаблон, кажется, этот приём используют для внутренних настроек
		(<scrip>) экземпляра Vue, которые повторяются в разных компонентах */

	
import BaseFormField from '@/components/BaseFormField';

export default {
	components: {BaseFormField},
	props: ['title', 'error', 'placeholder', 'value'],
		/* 	Помним, что принимаемое свойство "value" -- это именно значение введённое 
				в форму пользователем, которое мы можем принять обратившись именно по это
				имени ...
				А остальные параметры мы просто подставляем в компонент через
				атрибут v-bind:[имя]*/
	computed: {
		dataValue: {
			// двунаправленное вычисляемое совйство определяется как объекта, а...
			// внутри две функции get() и set()
			get(){
				return this.value;
			},
			set(value){
				this.$emit('input', value);
					/* 	ловим событие ввода значения в поле для ввода,
							передаём куда-то (?) дальше... 
							Это сделано как раз для изменения принимаемого извне значения
							value (см. раздел "props"), потому что напрямую менять принимаемые
							в компонент данные нельзя, а вот так (через двунаправленное свойство)
							вполне можно. */
			}
		}
	},
}